import React from 'react';
import { Table, Card } from 'antd';

const source = [
  {
    param: 'title',
    desc: '对话框标题',
    type: 'string',
    default: '-'
  },
  {
    param: 'content',
    desc: '对话框内容',
    type: 'ReactNode',
    default: '-'
  },
  {
    param: 'onShow',
    desc: '对话框打开后的回调',
    type: 'function',
    default: '-'
  },
  {
    param: 'onConfirm',
    desc: '点击对话框确认按钮后的回调',
    type: 'function(hide)',
    default: '-'
  },
  {
    param: 'inline',
    desc: '对话框的触发元素，是否包裹在行内元素中',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'hideFooter',
    desc: '是否隐藏默认的 footer',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'width',
    desc: '对话框宽度',
    type: 'number|string',
    default: 'false'
  },
  {
    param: 'confirmLoading',
    desc: '对话框确认按钮的 loading 状态',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'autoHide',
    desc: '点击对话框确认按钮后，是否自动关闭',
    type: 'boolean',
    default: 'true'
  }
];

const columns = [
  {
    title: '参数',
    dataIndex: 'param'
  },
  {
    title: '说明',
    dataIndex: 'desc'
  },
  {
    title: '类型',
    dataIndex: 'type'
  },
  {
    title: '默认值',
    dataIndex: 'default'
  }
];

const API = () => (
  <Card>
    <Table bordered pagination={false} dataSource={source} columns={columns} />
  </Card>
);

export default API;



