import React from 'react';
import { Card } from 'antd';
import Markdown from '../../components/Markdown';
import md from './index.md';
import DashboardApi from './DashboardApi';

const Summary = () => (
  <Card>
    <Markdown source={md} />
    <DashboardApi />
  </Card>
);

export default Summary;



