import React from 'react';
import { Table, Card, Divider } from 'antd';

const querySource = [
  {
    param: 'data',
    desc: '搜索域配置项',
    type: ' data : [ row : [ item ] ]',
    default: '-'
  },
  {
    param: 'form',
    desc: '搜索域所需的 antd form',
    type: 'object',
    default: '-'
  },
  {
    param: 'handleSearch',
    desc: '点击查询按钮后的回调，通常直接传入 Collection 提供的就行',
    type: 'function',
    default: '-'
  },
  {
    param: 'handleFormReset',
    desc: '点击重置按钮后的回调，通常直接传入 Collection 提供的就行',
    type: 'function',
    default: '-'
  }
];

const itemSource = [
  {
    param: 'label',
    desc: '搜索项标签名',
    type: 'string',
    default: '-'
  },
  {
    param: 'dataIndex',
    desc: '搜索项在 form 中对应的 key',
    type: 'string',
    default: '-'
  },
  {
    param: 'render',
    desc: '控件的渲染函数',
    type: 'Function(props) {} : ReactNode	',
    default: '-'
  },
  {
    param: 'simple',
    desc: '是否在收起状态下展示',
    type: 'boolean',
    default: 'false'
  },
  {
    param: 'options',
    desc: '配置项',
    type: 'object',
    default: '-'
  }
];

const columns = [
  {
    title: '参数',
    dataIndex: 'param'
  },
  {
    title: '说明',
    dataIndex: 'desc'
  },
  {
    title: '类型',
    dataIndex: 'type'
  },
  {
    title: '默认值',
    dataIndex: 'default'
  }
];

const API = () => (
  <Card>
    <h2>Query</h2>
    <Table
      bordered
      pagination={false}
      dataSource={querySource}
      columns={columns}
    />
    <div style={{ marginTop: '1rem' }}>每个 row 中的 item 数量为 1 - 3 个</div>
    <div style={{ marginTop: '1rem' }}>支持传入空 item 作为占位</div>
    <div style={{ marginTop: '1rem' }}>
      功能按钮（ 查询、重置、展开 / 收起 ）算作一个 item 的占位
    </div>
    <Divider />
    <h2>item</h2>
    <Table
      bordered
      pagination={false}
      dataSource={itemSource}
      columns={columns}
    />
    <div style={{ marginTop: '1rem' }}>
      详细配置项请参考
      <a href="https://github.com/react-component/form#option-object">
        rc-form option
      </a>
    </div>
  </Card>
);

export default API;



