import React from 'react';
import { CreateList } from 'mogul';

// 无需拉平数据
const mock = {
  name: {
    first: 'bruce',
    last: 'wayne'
  },
  gender: 'male',
  phone: '13333333333',
  email: 'mock@163.com',
  registered: '2018-04-25 14:57',
  location: {
    postcode: '215000'
  }
};
const columns = [
  {
    // label 文字
    title: '姓名',
    // React 所需的 key
    key: 'name',
    // 自定义渲染
    render: ({ name: { first, last } = {} }) => <div>{`${first} ${last}`}</div>
  },
  {
    title: '性别',
    key: 'gender',
    render: ({ gender }) => <div>{gender === 'female' ? '女' : '男'}</div>
  },
  {
    title: '电话号码',
    // 直接把 dataIndex 的目标值作为 string 传入，并当做 React 的 key
    dataIndex: 'phone'
  },
  {
    title: 'email',
    dataIndex: 'email'
  },
  {
    title: '注册时间',
    dataIndex: 'registered'
  },
  {
    title: '邮编',
    key: 'postcode',
    render: ({ location: { postcode } }) => <div>{postcode}</div>
  }
];

const MockList = () => {
  return CreateList(columns, {
    // 一个 list item 中，label 和 content 的占位值，加起来等于24
    // 下面例子中，label 宽和 content 宽的比例为 8 : 16，即 1 : 2
    labelCol: 8,
    contentCol: 16,
    // list 中一行有几个 item
    column: 2
  })(mock);
};

export default MockList;



