module.exports = "# 部署指南\n\n路由采用 history 模式，因此需要在服务端增加一个覆盖所有情况的候选资源：\n\n* 如果 URL 匹配不到任何静态资源，则应该返回同一个 index.html 页面，这个页面就是你 app 依赖的页面。\n\n因为我们的应用是个单页客户端应用，如果后台没有正确的配置，当用户在浏览器直接访问 http://oursite.com/user/id 就会返回 404，这就不好看了。\n\n## 后端配置例子\n\n**`Apache`**\n\n```Apache\n<IfModule mod_rewrite.c>\n  RewriteEngine On\n  RewriteBase /\n  RewriteRule ^index\\.html$ - [L]\n  RewriteCond %{REQUEST_FILENAME} !-f\n  RewriteCond %{REQUEST_FILENAME} !-d\n  RewriteRule . /index.html [L]\n</IfModule>\n```\n\n**`nginx`**\n\n```nginx\nlocation / {\n  try_files $uri $uri/ /index.html;\n}\n```\n\n**`原生 Node.js`**\n\n```js\nconst http = require('http');\nconst fs = require('fs');\nconst httpPort = 80;\n\nhttp\n  .createServer((req, res) => {\n    fs.readFile('index.htm', 'utf-8', (err, content) => {\n      if (err) {\n        console.log('We cannot open \"index.htm\" file.');\n      }\n\n      res.writeHead(200, {\n        'Content-Type': 'text/html; charset=utf-8'\n      });\n\n      res.end(content);\n    });\n  })\n  .listen(httpPort, () => {\n    console.log('Server listening on: http://localhost:%s', httpPort);\n  });\n```\n\n**`基于 Node.js 的 Express`**\n\n对于 Node.js/Express，请考虑使用 [connect-history-api-fallback](https://github.com/bripkens/connect-history-api-fallback) 中间件。\n\n**`Internet Information Services (IIS)`**\n\n1.  安装 [IIS UrlRewrite](https://www.iis.net/downloads/microsoft/url-rewrite)\n2.  在你的网站根目录中创建一个 `web.config` 文件，内容如下：\n\n```xml\n<?xml version=\"1.0\" encoding=\"UTF-8\"?>\n<configuration>\n  <system.webServer>\n    <rewrite>\n      <rules>\n        <rule name=\"Handle History Mode and custom 404/500\" stopProcessing=\"true\">\n          <match url=\"(.*)\" />\n          <conditions logicalGrouping=\"MatchAll\">\n            <add input=\"{REQUEST_FILENAME}\" matchType=\"IsFile\" negate=\"true\" />\n            <add input=\"{REQUEST_FILENAME}\" matchType=\"IsDirectory\" negate=\"true\" />\n          </conditions>\n          <action type=\"Rewrite\" url=\"/\" />\n        </rule>\n      </rules>\n    </rewrite>\n  </system.webServer>\n</configuration>\n```\n\n**`Caddy`**\n\n```Caddy\nrewrite {\n    regexp .*\n    to {path} /\n}\n```\n\n**`Firebase 主机`**\n\n在你的 `firebase.json` 中加入：\n\n```json\n{\n  \"hosting\": {\n    \"public\": \"dist\",\n    \"rewrites\": [\n      {\n        \"source\": \"**\",\n        \"destination\": \"/index.html\"\n      }\n    ]\n  }\n}\n```\n"

