module.exports = "# Mogul\n\nMogul (_['moɡl]_) 是针对食行生鲜中后台业务特别定制的相关开发规范和工具。使用 React、MobX、Ant Design 等工具实现，在此要感谢 Ant Design 提供了优秀的组件库。\n\nMogul 提供如下内容：\n\n1.  在 Ant Design 基础上的交互规范，以 Axure 为参考；\n1.  代码和命名推荐指南；\n1.  Mogul CLI 工具；\n1.  快速开始的文件结构；\n1.  再度封装的业务组件；\n1.  最佳实践指南。\n\n## 第一步：创建文件目录\n\n```bash\n$ npm i -g mogul-cli\n$ mogul new\n ? type project name please: PROJECT_NAME\n```\n\n## 第二步：探索文件目录\n\n```bash\nPROJECT_NAME                               # 生成的文件根目录\n  |- src                                   # 业务代码目录\n      |- index.js                          # 入口文件\n      |- index.css                         # 全局样式\n      |- pages                             # Tip: pages 目录下的一级文件夹以首字母大写驼峰法为准\n          |- App                           # App 路由一级入口\n              |- index.js\n          |- Dashboard                     # Dashboard 路由二级入口\n              |- index.js\n              |- menu.js                   # Sider 菜单项配置\n              |- Tool.js                   # Header 公共工具栏\n          |- Sign                          # Sign 路由二级入口\n              |- index.js\n          |- Homepage                      # Homepage 路由三级入口\n              |- index.js\n      |- utils                             # 工具方法目录\n          |- request.js                    # 封装 axios，处理通用错误\n          |- env.js                        # 封装环境变量，统一引用\n          |- routing.js                    # mobx router\n          |- token.js                      # 封装 token 存储函数\n  |- package.json\n  |- README.md                             # 项目安装、部署及文档入口\n  |- config-overrides.js                   # rewired 配置项\n  |- .eslintrc                             # 仅作用于编辑器的 eslint 设置\n  |- .prittierrc                           # 提交的代码必须通过 prittier 调整\n  |- .gitignore\n```\n\n## 第三步：探索 package.json\n\n```json\n{\n  \"name\": \"mogul-template\",\n  \"version\": \"0.0.1\",\n  \"private\": true,\n  \"license\": \"GPL\",\n  \"description\": \"mogul-template\",\n  \"repository\": \"https://github.com/freshesx/mogul-template.git\",\n  \"scripts\": {\n    \"start\": \"react-app-rewired start\",\n    \"build\": \"react-app-rewired build\",\n    \"module\": \"plop --plopfile ./node_modules/mogul/plopfile.js\",\n    \"test\": \"react-app-rewired test --env=jsdom\",\n    \"precommit\": \"lint-staged\"\n  },\n  \"lint-staged\": {\n    \"src/**/*.{js,jsx,json,css}\": [\"prettier --write\", \"git add\"]\n  },\n  \"dependencies\": {\n    \"mogul\": \"^0.1.6\",\n    \"prettier\": \"^1.12.1\"\n  },\n  \"devDependencies\": {\n    \"husky\": \"^0.14.3\",\n    \"lint-staged\": \"^7.0.4\"\n  }\n}\n```\n\n## 第四步：命令行快速创建\n\n```bash\n# 切换至 PROJECT_NAME 根目录\n$ npm run module\n  ? type module name please: Template\n```\n\n```bash\npages\n  |- App\n  |- Dashboard\n  |- Sign\n  |- Homepage\n  |- Template\n      |- index.js                # 入口文件\n      |- Collection.js           # 数据集展示\n      |- Query.js                # 查询条件\n      |- Editor.js               # 新建和更新页\n      |- Detail.js               # 详情页\n      |- style.module.css        # 样式文件\n      |- services\n          |- api.js              # 接口\n          |- pagination.js       # 分页 MobX\n          |- query.js            # 查询 MobX\n          |- collection.js       # 数据集 MobX\n      |- __test__                # 测试文件目录\n```\n\n建议为一个实体(Entity)数据新建一个 module，如果是 Project 实体下关联的子实体，如 Project Category，则新建 module：\n\n```bash\n$ npm run module\n ? type module name please: ProjectCategory\n```\n\n```bash\npages\n  |- App\n  |- Dashboard\n  |- Sign\n  |- Homepage\n  |- Projects\n  |- ProjectCategories\n```\n\n## 第五步：了解路由的运作机制\n\nMogul Router 通过 React Router 实现了路由业务，在 Mogul 和 React Router 哲学里，一切皆组件。\n\n### 路由树结构与项目目录结构的对应关系\n\n`路由结构图`\n\n```bash\n                                           # 对应的目录文件\n/                                          # pages/App/index.js\n  |- /dashboard                            # pages/Dashboard/index.js\n      |- /dashboard/projects               # pages/Projects/index.js\n          |- /dashboard/projects/:id       # pages/Projects/Collection.js\n          |- /dashboard/projects/:id/edit  # pages/Projects/Editor.js\n      |- /dashboard/project-categories     # pages/ProjectCategories/index.js\n  |- /sign                                 # pages/Sign/index.js\n      |- /sign/in                          # pages/Sign/In.js\n      |- /sign/up                          # pages/Sign/Up.js\n```\n\n`目录结构图`\n\n```bash\npages\n  |- App\n      |- index.js\n  |- Dashboard\n      |- index.js\n  |- Projects\n      |- index.js\n      |- Collection.js\n      |- Detail.js\n      |- 更多完整目录查看第二步\n  |- ProjectCategories\n  |- Sign\n```\n\n### 路由代码\n\n`src/pages/App/index.js`\n\n```js\nimport React from 'react';\nimport { Route, Switch, Redirect } from 'react-router';\nimport { App } from 'mogul';\nimport routing from '../../utils/routing';\nimport Dashboard from '../Dashboard';\n\nexport default () => {\n  return (\n    <App routing={routing}>\n      <Switch>\n        <Redirect from=\"/\" to=\"/dashboard\" exact />\n        <Route path=\"/dashboard\" component={Dashboard} />\n      </Switch>\n    </App>\n  );\n};\n```\n\n`src/pages/Dashboard/index.js`\n\n```js\nimport React from 'react';\nimport { Dashboard } from 'mogul';\nimport { Route, Switch } from 'react-router';\nimport { SYSTEM_FOOTER, SYSTEM_NAME } from '../../utils/env';\nimport routing from '../../utils/routing';\nimport menu from './menu';\nimport styles from './styles.module.css';\nimport Tool from './Tool';\nimport Homepage from '../Homepage';\nimport Template from '../Template';\n// do not delete this Note!\n// insert:component\n\nexport default ({ match: { url } }) => (\n  <Dashboard\n    tool={<Tool />}\n    routing={routing}\n    name={SYSTEM_NAME}\n    footer={SYSTEM_FOOTER}\n    menu={menu}\n    logo={<div className={styles.logo} />}\n    url={url}\n  >\n    <Switch>\n      <Route path={`${url}`} component={Homepage} exact />\n      <Route path={`${url}/template`} component={Template} />\n      {/*do not delete this Note!*/}\n      {/*insert:route*/}\n    </Switch>\n  </Dashboard>\n);\n```\n\n`src/pages/Projects/index.js`\n\n```js\nimport React from 'react';\nimport { Route, Switch } from 'react-router';\nimport Collection from './Collection.js';\nimport Detail from './Detail.js';\nimport Editor from './Editor.js';\n\nconst Routes = ({ match: { url } }) => {\n  return (\n    <Switch>\n      <Route path={url} component={Collection} exact />\n      <Route path={`${url}/:id`} component={Detail} exact />\n      <Route path={`${url}/edit/:id`} component={Editor} exact />\n    </Switch>\n  );\n};\n\nexport default Routes;\n```\n\n## 进一步阅读\n\n* [@mogul/cli 的接口文档](cli.md)\n* [类库](libraries.md)\n* 掌握常用的 Mogul router 和相关的 Params、QueryString 处理\n* 基于 MobX 的状态管理\n* 常见的范式布局\n* [通用的命名规范](rules.md)\n* 嵌套关联实体的 CURD 最佳实践\n\n## 相关资料\n\n* [React 中文文档](https://doc.react-china.org)\n* [MobX 中文文档](http://cn.mobx.js.org)\n* [Ant Design 组件文档](https://ant.design/docs/react/introduce-cn)\n"

