import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Input, Form } from 'antd';
import { CreateFormItem } from 'mogul';

// 本组件基于 antd 的 Form
// 建议参阅: https://ant.design/components/form-cn/

const MockFormItem = props => {
  const { form, firstName, lastName } = props;
  return CreateFormItem(form, {
    // 展示用的标签名
    label: '姓名',
    // React 所需的 key
    key: 'name',
    option: {
      // initialValue 是 form field 中的默认值
      initialValue: `${firstName} ${lastName}`,
      // 定义校验规则
      rules: [{ required: true, message: '请输入姓名' }]
    },
    // 定义渲染控件，基于 HOC 设计，此处的 props 和 MockFormItem 的 props 一致
    render: props => <Input placeholder="请输入姓名" />
  })(props);
};

MockFormItem.propTypes = {
  form: PropTypes.object.isRequired
};

// FormItem 一定要基于 antd 的 form 使用
@Form.create()
class MockForm extends Component {
  mock = {
    firstName: 'bruce',
    lastName: 'wayne'
  };
  render() {
    const { form } = this.props;
    return <MockFormItem form={form} {...this.mock} />;
  }
}

export default MockForm;



