import React from 'react';
import { Table } from 'antd';

const source = [
  {
    prop: 'tool',
    desc: 'Header 中的工具组件',
    type: 'ReactNode',
    default: '-'
  },
  {
    prop: 'routing',
    desc: '路由 store',
    type: 'object',
    default: '-'
  },
  {
    prop: 'name',
    desc: '系统名称',
    type: 'string',
    default: '-'
  },
  {
    prop: 'footer',
    desc: '系统脚注',
    type: 'string',
    default: '-'
  },
  {
    prop: 'menu',
    desc: 'Sider 配置项',
    type: 'array',
    default: '-'
  },
  {
    prop: 'logo',
    desc: 'Sider logo 图标',
    type: 'ReactNode',
    default: '-'
  },
  {
    prop: 'url',
    desc: '上层 match url',
    type: 'string',
    default: '-'
  }
];

const columns = [
  {
    title: '属性',
    dataIndex: 'prop'
  },
  {
    title: '说明',
    dataIndex: 'desc'
  },
  {
    title: '类型',
    dataIndex: 'type'
  },
  {
    title: '默认值',
    dataIndex: 'default'
  }
];

const API = () => (
  <Table bordered pagination={false} dataSource={source} columns={columns} />
);

export default API;



