import React, { Component } from 'react';
import { Input, Select, Form } from 'antd';
import { Query } from 'mogul';

const { Option } = Select;

const columns = [
  [
    {
      label: '名称',
      dataIndex: 'name',
      render: () => (
        <Input style={{ width: '100%' }} placeholder="请输入名称" />
      ),
      simple: true,
      options: {
        rules: [{ required: true, message: '请选择配送日期' }]
      }
    },
    {
      label: '状态',
      dataIndex: 'status',
      render: () => (
        <Select placeholder="请选择状态" style={{ width: '100%' }}>
          <Option key={1}>111</Option>
        </Select>
      )
    }
  ]
];

@Form.create()
class MockCollection extends Component {
  render() {
    const { form } = this.props;
    return (
      <Query
        data={columns}
        options={{}}
        form={form}
        handleSearch={event => {
          event.preventDefault();
        }}
        handleFormReset={() => {}}
      />
    );
  }
}

export default MockCollection;



