import React from 'react';
import { Dashboard } from 'mogul';
import { Route, Switch } from 'react-router';
import { SYSTEM_FOOTER, SYSTEM_NAME } from '../../utils/env';
import routing from '../../utils/routing';
import menu from './menu';
import styles from './styles.module.css';
import Homepage from '../Homepage';
// do not delete this Note!
// insert:component
import QuickStart from '../QuickStart';
import Summary from '../Summary';
import CreateFormItem from '../CreateFormItem';
import CreateList from '../CreateList';
import Modal from '../Modal';
import Query from '../Query';
import Base from '../Base';
import Deploy from '../Deploy';
import StandardTable from '../StandardTable';

export default ({ match: { url } }) => (
  <Dashboard
    routing={routing}
    name={SYSTEM_NAME}
    footer={SYSTEM_FOOTER}
    menu={menu}
    logo={<div className={styles.logo} />}
    url={url}
  >
    <Switch>
      <Route path={`${url}`} component={Homepage} exact />
      {/*do not delete this Note!*/}
      {/*insert:route*/}
      <Route path={`${url}/quick-start`} component={QuickStart} exact />
      <Route path={`${url}/summary`} component={Summary} exact />
      <Route
        path={`${url}/create-form-item`}
        component={CreateFormItem}
        exact
      />
      <Route path={`${url}/create-list`} component={CreateList} exact />
      <Route path={`${url}/modal`} component={Modal} exact />
      <Route path={`${url}/query`} component={Query} exact />
      <Route path={`${url}/base`} component={Base} exact />
      <Route path={`${url}/deploy`} component={Deploy} exact />
      <Route path={`${url}/standard-table`} component={StandardTable} exact />
    </Switch>
  </Dashboard>
);



