import React, { Component, Fragment } from 'react';
import Display from '../../components/Display';
import { Card, Divider, Row, Col } from 'antd';
import StandardTable from './StandardTable';
import markdown from './index.md';

class MockStandardTable extends Component {
  render() {
    return (
      <Fragment>
        <Card>
          <h1>StandardTable</h1>
          标准表格
        </Card>
        <Divider />
        <Row gutter={24}>
          <Col span={24}>
            <Display markdown={markdown} title="基本用法" desc="介绍基本用法">
              <StandardTable />
            </Display>
          </Col>
        </Row>
      </Fragment>
    );
  }
}

export default MockStandardTable;



